<template>
  <div>
  <h2登陆界面></h2>
  用户名:
  <el-input
  type="text"
  placeholder="请输入用户名"
  v-model="logInfo.username"
  clearable> 
</el-input>


  密码:<el-input
  type="password"
  placeholder="请输入密码"
  v-model="logInfo.password"
  clearable>
</el-input>
   <el-radio v-model="logInfo.type" label=1>医生</el-radio>
  <el-radio v-model="logInfo.type" label=2>病人</el-radio>
  <button @click="login()">登陆</button>
  </div>
</template>

<script>
export default {
    data() {
      return {
          logInfo:{
              type:0,
              username:'',
              password:''
          }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      login()
      {
            this.$axios.post('/login', {
            type: this.logInfo.type,
            username: this.logInfo.username,
            password: this.logInfo.password
        })
        .then(response=>{
            console.log(response);
            if(response.data.code==200)
            {
              this.$router.push('/demo1');
            }
            else
            {
              alert("登录失败");
            }
        })
        .catch(function (error) {
            console.log(error);
        });
            
     }
    }
}
</script>

<style>

</style>



