<template>
     <div>
         <h2>{{msg}}</h2>
         第一个VUE
         <h1>{{student.name}}</h1>
         <ul>
         <li v-for="stu in list">{{stu.name}}</li>
         </ul>

         <ul>
            <li v-for="cn in list2">
                 {{cn.classname}}
                 <ul>
                     <li v-for="s in cn.students">{{s.name}}</li>
                 </ul>
            </li>
         </ul>
         <button @click="goTodemo1()">跳转demo1</button>
     </div>
</template>

<script>
     export default {
       name: 'HelloWorld',
       data () {
          return {
          msg: '我是你爸爸',
          student:{
               name:'吴彦祖'
          },
          list:[
               {name:'周星驰'},
               {name:'库兹马'},
               {name:'渣渣辉'}
          ],
          list2:[
           {
               classname:'赌圣',
               students:[{name:'周星驰'},
               {name:'库兹马'},
               {name:'渣渣辉'}]
           },
           {
                classname:'赌侠',
                students:[
                     {name:'陈奕迅'},
                     {name:'周杰伦'},
                     {name:'林俊杰'}
                ]
           }
          ]
          }
     },
     methods:
     {
         goTodemo1(){
             this.$router.push('/')
         }
     }
}
</script>

<style>
</style>


